@import url(https://fonts.googleapis.com/css2?family=Lato&family=Playfair+Display:wght@500&display=swap);
:root {
  --blue: hsla(197, 37%, 24%, 1);
  --green: hsla(173, 58%, 35%, 1);
  --yellow: hsla(43, 74%, 66%, 1);
  --light-yellow: hsla(43, 74%, 76%, 1);
  --orange: hsla(27, 87%, 62%, 1);
  --red: hsla(12, 76%, 61%, 1);
  --main-font: 'Playfair Display', serif;
  --second-font: 'Lato', sans-serif;
}

.app {
  display: grid;
  grid-template-areas:
    'header header header'
    'sidebar main main'
    'footer footer footer';
  grid-template-rows: 100px calc(100vh - 110px) 10px;
  grid-template-columns: 200px auto auto;
  font-family: 'Lato', sans-serif;
  font-family: var(--second-font);
}

.app__header {
  grid-area: header;
  background-color: hsla(197, 37%, 24%, 1);
  background-color: var(--blue);
  padding: 18px 15px;
}

.app__subtitle {
  font-size: 1em;
  color: hsla(27, 87%, 62%, 1);
  color: var(--orange);
  font-family: 'Lato', sans-serif;
  font-family: var(--second-font);
}

.app__title {
  font-family: 'Playfair Display', serif;
  font-family: var(--main-font);
  color: hsla(43, 74%, 66%, 1);
  color: var(--yellow);
  font-size: 2em;
}

.app__sidebar {
  grid-area: sidebar;
}

.app__main {
  background-color: hsla(173, 58%, 35%, 1);
  background-color: var(--green);
  grid-area: main;
  padding: 15px 30px 0px;
  position: relative;
  overflow: auto;
}

.app__sidebar {
  background-color: hsla(197, 37%, 24%, 1);
  background-color: var(--blue);
  color: hsla(43, 74%, 66%, 1);
  color: var(--yellow);
  padding: 0px 15px;
}

.app__sidebar_option {
  font-family: 'Lato', sans-serif;
  font-family: var(--second-font);
  font-size: 0.95em;
  padding: 15px 0px;
  transition: 0.4s all;
}

.app__sidebar_option:hover {
  font-size: 1em;
  color: hsla(27, 87%, 62%, 1);
  color: var(--orange);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.app__footer {
  grid-area: footer;
  background-color: hsla(12, 76%, 61%, 1);
  background-color: var(--red);
}

.example__container {
  height: 500px;
}

.example__title {
  font-family: var(--main-font);
  padding: 20px 0px 15px;
  font-size: 2em;
  color: var(--orange);
  text-shadow: 1px 1px 12px rgba(2, 2, 5, 0.4);
}

.example__description {
  text-shadow: 1px 1px 6px rgba(2, 2, 5, 0.2);
  color: var(--yellow);
}

.example__children {
  padding: 25px 0px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

